import React from 'react'

class App extends React.Component {
    constructor(){
        super()
        this.state = {
            firstName : ""
        }
        this.handleChange = this.handleChange.bind(this)
    }

    handleChange(event){
        this.setState({
            firstName : event.target.value
        })
    }

    render(){
        return (
            <div>
                <form>
                    <input type='text' placeholder="Try to write something" onChange={this.handleChange}/>
                </form>
                <h1>{this.state.firstName}</h1>
            </div>
        )
    }
}

export default App